<template>
    <div class="page-container">
        <div class="article-container">
            <ul class="article-info">
                <li class="bname">
                    {{ boardInfo.bname }}
                </li>
                <li class="bsubject">
                    <span v-if="info.category" class="category">[{{categoryNames[info.category]}}]</span> {{
                    info.subject }}
                    <span v-if="info.secret=='yes'">
                       <font-awesome-icon icon="fa-solid fa-lock"/>
                    </span>
                </li>
                <li class="summary">
                    <div class="summary-info">
                        <span class="info-list">작성자  <span>{{ userName }}</span></span>
                        <span class="info-list">작성일자  <span>{{ info.created_at }}</span></span>
                        <span class="info-list">조회수  <span>{{ info.hit }}</span></span>
                    </div>
                </li>
            </ul>
            <div v-if="info.img" class="img">
                <img :src="info.img"/>
            </div>
            <div v-html="info.content" class="content">
            </div>
            <div class="down-file" v-if="fileList.length>0">
                <div class="down-file-title">첨부파일</div>
                <ul>
                    <li v-for="(file,index) in fileList" @click="fileDown(file)">{{file.name}}</li>
                </ul>
            </div>
            <div class="button-row">
                <ul class="keywords">
                    <li v-for="key in keywords">
                        #{{key}}
                    </li>
                </ul>
                <div class="button-box">
                    <v-btn class="whiteButton" v-if="owner" @click="modifyItem()" type="button" depressed>
                        <span>수정</span></v-btn>
                    <v-btn class="red-button" v-if="owner" @click="deleteItem()" depressed><span>삭제</span></v-btn>
                    <v-btn @click="$router.back()" class="blackButton" depressed type="button"><span>목록</span></v-btn>
                </div>


            </div>
            <commentList v-if="replyShow" ptype="board" @updateCommentCnt="updateCommentCnt"
                         :pid="id" :boardId="bid"></commentList>
        </div>

        <articleUpassComponent :bid="bid" :articleId="id" :type="upassType" :mcode="mcode" :scode="scode"
                               :modelOpen="passDialogShow"
                               @upassSuccess="delAction"
                               @parentOpenFalse="passDialogShow=false"></articleUpassComponent>
    </div>
</template>


<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_ARTICLE_INFO,
        API_URL_DELETE_ARTICLE
    } from '@/config/web/urls'
    import AUTH from "@/config/web/auth";
    import articleUpassComponent from '@/components/articleUpass'
    import commentList from '@/views/WEB/comments/commentList'

    import JQ from 'jquery'
    import UTIL from "@/common/util";
    import {mapActions} from "vuex";

    export default {
        components: {
            articleUpassComponent,
            commentList

        },
        data() {
            return {
                passDialogShow: false,
                bid: '',
                id: '',
                mcode: '',
                scode: '',
                util: UTIL,
                categoryNames: {},
                info: {},
                boardInfo: {},
                owner: false,
                userName: '',
                memberInfo: {},
                replyShow: false,
                upassType: 'modify',
                commentCnt: 0,
                keywords: [],
                fileList: [],
                isLogin: false,
            };
        },
        created() {
            this.id = this.$route.params.id;
            this.mcode = this.$route.params.mainCode;
            this.scode = this.$route.params.subCode;

            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.memberInfo = member.memberInfo;
                this.isLogin = true;
            }
            this.getData();
        },
        methods: {
            ...mapActions('config', ["setMemberNoticeList", "setMNoticeActive"]),
            getData() {
                let frm = {id: this.id};
                if (!this.isLogin && sessionStorage.getItem('articleSessPass')) {
                    frm.sessPass = sessionStorage.getItem('articleSessPass');
                }
                apiService.post(API_URL_GET_ARTICLE_INFO, frm, (res) => {
                    this.boardInfo = res.board;
                    if (this.boardInfo.categoryUse == 'yes') {
                        for (const item of JSON.parse(this.boardInfo.categoryList)) {
                            this.categoryNames[item.code] = item.name;
                        }
                    }
                    this.info = res.info;
                    this.bid = this.info.bid;
                    if (this.info.replyUse == 'yes') {
                        this.replyShow = true;

                    }

                    if (this.info.keywords) {
                        let keywords = this.info.keywords.split(',');
                        for (const key of keywords) {
                            if (key) {
                                this.keywords.push(key);
                            }
                        }

                    }
                    if (this.info.dfileOrgNames) {
                        let fileOrgNames = this.info.dfileOrgNames.split(',');
                        let fileNames = this.info.dfileNames.split(',');
                        let fIndex=0;
                        for(const fname of fileOrgNames) {
                            let file = fileNames[fIndex];
                            this.fileList.push({name:fname,file:file})
                            fIndex++;
                        }

                    }


                    this.userName = this.info.userName;
                    this.commentCnt = (this.info.commentCnt) ? UTIL.numberFormat(this.info.commentCnt) : 0;
                    this.info.hit = (this.info.hit) ? UTIL.numberFormat(this.info.hit) : 0;
                    this.info.created_at = this.info.createAt.substring(0, 16).replace('T', ' ');

                    if (this.memberInfo.role == 'ROLE_ADMIN') this.owner = true;
                    else if (this.info.userId && this.info.userId == this.memberInfo.uid) this.owner = true;
                    else if (!this.info.userId && this.boardInfo.wauth == 'all') this.owner = true;

                    if (this.isLogin) {
                        if (res.memberNoticeList) {
                            let memberNoticeList = [];
                            for (let item of res.memberNoticeList.content) {
                                item.noticeContent = (item.gtype == 'comment') ? '님이 내글에 새로운 댓글을 달았습니다' : '새 공지사항이 등록되었습니다';
                                memberNoticeList.push(item);
                            }

                            this.setMemberNoticeList(memberNoticeList)
                            this.setMNoticeActive(true)
                        }
                    }

                })
            },
            modifyItem() {
                if (this.memberInfo.role == 'ROLE_ADMIN') {
                    this.$router.push('/board/articleRegist/' + this.mcode + '/' + this.scode + '/' + this.bid + '/' + this.id)
                } else if (this.info.userId && this.info.userId == this.memberInfo.uid) {
                    this.$router.push('/board/articleRegist/' + this.mcode + '/' + this.scode + '/' + this.bid + '/' + this.id)

                } else if (!this.info.userId && this.boardInfo.wauth == 'all') {
                    this.upassType = 'modify';
                    this.passDialogShow = true;
                }

            },
            fileDown(file) {
                let url = apiService.imgHost + 'api/controller/board/fileDownload?articleId='+this.id+'&fileName=' + encodeURIComponent(file.file)+'&viewFileName=' + encodeURIComponent(file.name)
                location.href= url;
            },
            deleteItem() {
                let directDelete = false;
                if (this.memberInfo.role == 'ROLE_ADMIN') {
                    directDelete = true;
                } else if (this.info.userId && this.info.userId == this.memberInfo.uid) {
                    directDelete = true;
                } else if (!this.info.userId && this.boardInfo.wauth == 'all') {
                    this.upassType = 'delete';
                    this.passDialogShow = true;
                }
                if (directDelete) {
                    const params = {
                        type: 'confirm',
                        message: '게시글을 삭제 하시겠습니까?',
                        doAction: () => {
                            this.deleteAction()
                        }
                    }
                    this.$eventBus.$emit('modalOpen', params);
                }

            },
            delAction(params) {
                this.deleteAction();
            },
            updateCommentCnt(params) {
                this.commentCnt = UTIL.numberFormat(params.total);
            },
            deleteAction() {
                const frm = {id: this.id};
                if (!this.info.userId && this.boardInfo.wauth == 'all') {
                    if (!sessionStorage.getItem('articleSessPass')) {
                        return;
                    }
                    frm.sessPass = sessionStorage.getItem('articleSessPass').toString();
                }
                apiService.post(API_URL_DELETE_ARTICLE, frm, (res) => {
                    const params = {
                        message: '게시글이 삭제 되었습니다',
                        doAction: () => {
                            this.$router.push('/board/articleList/' + this.mcode + '/' + this.scode + '/' + this.bid)
                        }
                    }
                    this.$eventBus.$emit('modalOpen', params);
                })
            },
        },
        updated() {
            JQ('.content img').css('max-width', '100%')
        }


    }
</script>

<style lang="scss" scoped>
    @import 'src/assets/style/common';

    .article-container {
        width: 100%;
        margin: 0;
        padding: 21px 24px;
        min-height: calc(100vh - 300px);
        background: #fff;

        .article-info {
            padding: 0 0 10px 0;
            margin: 0;

            .bname {
                font-weight: bold;
                font-size: 18px;
                color: #333;
                padding-bottom: 10px;
                border-bottom: solid 1px #333;

            }

            .bsubject {
                padding: 15px 0 5px 10px;
                font-weight: bold;
                font-size: 18px;
                color: #333;

                .category {
                    font-size: 18px;
                }
            }

            .summary {
                padding: 0 0 7px 10px;
                font-size: 12px;
                display: flex;
                border-bottom: solid 1px #dcdcdc;
                justify-content: space-between;

                .summary-info {
                    .info-list {
                        padding-right: 20px;
                        color: #999999;

                        span {
                            color: #555555;
                        }
                    }
                }

            }
        }

        .img {
            padding: 20px 0;
            border-bottom: solid 1px #999;
        }

        .content {
            padding: 15px 10px;
            margin-bottom: 30px;

            img {
                max-width: 100% !important;
            }
        }

        .down-file {
            padding: 0 10px;

            .down-file-title {
                font-weight: bold;
                font-size: 14px;
                color: #333;
                padding-bottom: 5px;
            }

            ul {
                padding: 0;

                li {
                    font-size: 12px;
                    padding-top: 3px;
                    cursor: pointer;
                }
            }
        }
    }

    .button-row {
        display: flex;
        justify-content: space-between;
        padding: 30px 10px 0 10px;

        .keywords {
            width: 95%;
            padding: 0;
            display: flex;
            flex-wrap: wrap;

            li {
                font-size: 12px;
                color: #999999;
                padding-right: 10px;
            }
        }

        .button-box {
            min-width: 100px;
            width: 100px;
            display: flex;
            justify-content: right;

            .whiteButton {
                width: 43px;
                height: 25px;
                min-width: 43px;
                min-height: 25px;
                max-height: 25px;
                margin-right: 10px;
                border: solid 1px #333;

                span {
                    font-size: 12px;
                    font-weight: normal !important;
                }
            }

            .blackButton {
                width: 43px;
                height: 25px;
                min-width: 43px;
                min-height: 25px;
                max-height: 25px;

                span {
                    font-size: 12px;
                    font-weight: normal !important;
                }
            }

            .red-button {
                width: 43px;
                height: 25px;
                min-width: 43px;
                min-height: 25px;
                max-height: 25px;
                background: #880E4F;
                color: #fff;
                margin-right: 10px;

                span {
                    font-size: 12px;
                    font-weight: normal !important;

                }
            }
        }
    }
    @media screen  and (max-width: 1259px) {


        .button-row {
            display:block !important;
            .keywords {
                width:100%;
                padding: 0 0 30px 0 !important;
            }
            .button-box {

                width:100%;
                justify-content: center !important;
            }
        }
    }
</style>